export class ListaCorridas {

    constructor() {
        
        this._corridas = [];
    }

    get corridas() {
        
        return [].concat(this._corridas);
    }
    
    adiciona(corrida) {
        
        this._corridas.push(corrida);
    }

    esvazia() {
        
        this._corridas = [];
    }

    ordena(criterio) {

        this._corridas.sort(criterio);        
    }
    
    inverteOrdem() {

        this._corridas.reverse();
    }    
}